</template>
  
<script>
import { event } from 'vue-gtag'

export default {
  name: 'MainFooter',
  data() {
     return {
      showPolicyPopup: false, // 
      showInfoPopup: false, // 
      mobile: false,
     };
   },
  watch: {
    showPolicyPopup(newValue) {
      if (newValue) {
        this.disableScroll();
      } else {
        this.enableScroll();
      }
    },
    showInfoPopup(newValue) {
      if (newValue) {
        this.disableScroll();
      } else {
        this.enableScroll();
      }
    },
  },
  methods: {
    policyPopup() {
      this.showPolicyPopup = !this.showPolicyPopup; // /
    },
    infoPopup() {
      this.showInfoPopup = !this.showInfoPopup; // /
    },
    closePolicyPopup() {
      this.showPolicyPopup = false; // 
      // gat
      event('');
    },
    closeInfoPopup() {
      this.showInfoPopup = false; // 
      // gat
      event('');
    },
    isMobile() {
      var userAgent = window.navigator.userAgent.toLowerCase()
      if (
        userAgent.indexOf("iphone") != -1 ||
        userAgent.indexOf("ipad") != -1 ||
        userAgent.indexOf("android") != -1 ||
        userAgent.indexOf("mobile") != -1
      ) {
        return true
      } else {
        return false
      }
    },
    disableScroll() {
      document.body.style.overflow = 'hidden'; // 
    },
    enableScroll() {
      document.body.style.overflow = ''; // 
    },
  },
  mounted() {
    if (this.isMobile()) {
      this.mobile = true;
    } else {
      this.mobile = false;
    }
  },
}
</script>
