                        <img @click="toggleVerseItem(verseData.value)" class="cordeItemImg" :class="{'wishVerseBorder': wishVerseItems.includes(verseData.value)}" v-lazy="require(`@/img/verse/${verseData.src}`)" alt="">
                        <span>{{ verseData.name }}</span>
                      </div>
                    </li>
                  </ul>
                </div>
                <div v-show="(selectedVerseRank === 'sp' || selectedVerseRank === 0)">
                  <img v-show="filteredVerseListSP[5].length > 0" class="starClass starMargin" :src="require(`@/img/icon/special.webp`)" alt="">
                  <ul id="">
                    <li v-for="(verseData) in filteredVerseListSP[5]" :key="verseData.value" class="itemLi">
                      <div class="tooltip1" :class="dynamicClass(verseData)">
                        <img @click="toggleVerseItem(verseData.value)" class="cordeItemImg" :class="{'wishVerseBorder': wishVerseItems.includes(verseData.value)}" v-lazy="require(`@/img/verse/${verseData.src}`)" alt="">
                        <span>{{ verseData.name }}</span>
                      </div>
                    </li>
                  </ul>
              </div>
            </div>
          </div>
        </div>
        </li>
      </ul>
    </div>
  </div>
  <div class="top_btn" v-show="buttonActive" @click="pageTop">
  </div>
</template>
 
<script>
import himitsuJson from '../../json/himitsuItem.JSON'
import verseJson from '../../json/verseItem.JSON'
//import Cookies from 'js-cookie';  // js-cookie 
import 'font-awesome/css/font-awesome.css';
import { signInWithPopup, GoogleAuthProvider, signOut, onAuthStateChanged, setPersistence, browserLocalPersistence } from "firebase/auth";
import { getFirestore, doc, getDoc, setDoc } from "firebase/firestore";
import { auth } from '../firebaseConfig'; // Firebase
import { event } from 'vue-gtag'
import WishHimitsu from './WishHimitsu.vue'
import WishVerse from './WishVerse.vue'

// ()
const iniVersion = 4;
 
export default {
  name: "MainBody",
  components: {
    WishHimitsu,
    WishVerse,
  },
  data() {
    return {
      isActive: "A",
      selectedHimitsuVersion: iniVersion,
      selectedHimitsuGet: 0,
      selectedHimitsuRank: 0,
      selectedHimitsuChar: 0,
      selectedVerseVersion: iniVersion,
      selectedVerseGet: 0,
      selectedVerseRank: 0,
      selectedVerseBrand: 0,
      himitsuItemName: "", // 
      verseItemName: "", // 
      iniHimitsuList: himitsuJson,
      //himitsuList: himitsuJson,
      iniVerseList: verseJson,
      //verseList: verseJson,
      selectedItems: [],
      wishHimitsuItems: [],
      wishVerseItems: [],
      mobile: false,
      buttonActive: false,//
      scroll: 0,
      userPhotoURL: "",
      isLoggedIn: false, // 
      isLoading: true, // 
      showUser: false, // 
      showPopup: false, // 
      showHelpPopup: false, // 
      user: "",
      kind: "", //
      displayedItems: [], // 
      itemsToShow: 10, // 
      loading: false, // 
      items: Array.from({ length: 1000 }, (_, index) => `Item ${index + 1}`),
      itemSize: 30, // 
      showWishHimitsu: false,
      showWishVerse: false,
      wishHimitsuMode: false,
      wishVerseMode: false,
    };
  },
  computed: {
    filteredHimitsuList() {
      let list = this.iniHimitsuList;
      // 
      if (this.selectedHimitsuVersion !== 0) {
        list = list.filter(
          (item) => item.version === String(this.selectedHimitsuVersion)
        );
      }
  
      // 
      if (this.selectedHimitsuGet === 1) {
        // 
        list = list.filter((item) => this.selectedItems.includes(item.value));
      } else if (this.selectedHimitsuGet === 2) {
        // 
        list = list.filter((item) => !this.selectedItems.includes(item.value));
      } else if (this.selectedHimitsuGet === 3) {
        // 
        list = list.filter((item) => this.wishHimitsuItems.includes(item.value));
      } 
  
      // 
      if (this.selectedHimitsuChar !== 0) {
        //console.log(this.selectedVerseBrand);
        // selectedVerseBrand"ph"brand
        list = list.filter((item) => item.char === this.selectedHimitsuChar);
      }
  
      // 
      if (this.himitsuItemName) {
        const nameLower = this.himitsuItemName.toLowerCase(); // 
        list = list.filter((item) =>
          item.name.toLowerCase().includes(nameLower)
        );
      }
      //console.log(list);
      return list;
    },
    filteredVerseList() {
      let list = this.iniVerseList;
  
      // 
      if (this.selectedVerseVersion !== 0) {
        list = list.filter(
          (item) => item.version === String(this.selectedVerseVersion)
        );
      }
  
      // 
      if (this.selectedVerseGet === 1) {
        // 
        list = list.filter((item) => this.selectedItems.includes(item.value));
      } else if (this.selectedVerseGet === 2) {
        // 
        list = list.filter((item) => !this.selectedItems.includes(item.value));
      } else if (this.selectedVerseGet === 3) {
        // 
        list = list.filter((item) => this.wishVerseItems.includes(item.value));
      } 
  
      // 
      if (this.selectedVerseBrand !== 0) {
        //console.log(this.selectedVerseBrand);
        // selectedVerseBrand"ph"brand
        list = list.filter((item) => item.brand === this.selectedVerseBrand);
      }
  
      // 
      if (this.verseItemName) {
        const nameLower = this.verseItemName.toLowerCase(); // 
        list = list.filter((item) =>
          item.name.toLowerCase().includes(nameLower)
        );
      }
      return list;
    },
  
    // 
    himitsuList1() {
      return this.filteredHimitsuList.filter((item) => item.version === "1");
    },
    himitsuList2() {
      return this.filteredHimitsuList.filter((item) => item.version === "2");
    },
    himitsuList3() {
      return this.filteredHimitsuList.filter((item) => item.version === "3");
    },
    himitsuList4() {
      return this.filteredHimitsuList.filter((item) => item.version === "4");
    },
    himitsuListSP() {
      return this.filteredHimitsuList.filter((item) => item.version === "sp");
    },
    himitsuListGumi() {
      return this.filteredHimitsuList.filter((item) => item.version === "gumi");
    },
    himitsuListMirufi() {
      return this.filteredHimitsuList.filter((item) => item.version === "mirufi");
    },
    verseList1() {
      return this.filteredVerseList.filter((item) => item.version === "1");
    },
    verseList2() {
      return this.filteredVerseList.filter((item) => item.version === "2");
    },
    verseList3() {
      return this.filteredVerseList.filter((item) => item.version === "3");
    },
    verseList4() {
      return this.filteredVerseList.filter((item) => item.version === "4");
    },
    verseListSP() {
      return this.filteredVerseList.filter((item) => item.version === "sp");
    },
    filteredVerseList1() {
      return {
        2: this.verseList1.filter(item => item.rank === 2),
        3: this.verseList1.filter(item => item.rank === 3),
        4: this.verseList1.filter(item => item.rank === 4),
      };
    },
    filteredVerseList2() {
      return {
        2: this.verseList2.filter(item => item.rank === 2),
        3: this.verseList2.filter(item => item.rank === 3),
        4: this.verseList2.filter(item => item.rank === 4),
      };
    },
    filteredVerseList3() {
      return {
        2: this.verseList3.filter(item => item.rank === 2),
        3: this.verseList3.filter(item => item.rank === 3),
        4: this.verseList3.filter(item => item.rank === 4),
      };
    },
    filteredVerseList4() {
      return {
        2: this.verseList4.filter(item => item.rank === 2),
        3: this.verseList4.filter(item => item.rank === 3),
        4: this.verseList4.filter(item => item.rank === 4),
      };
    },
    filteredVerseListSP() {
      return {
        2: this.verseListSP.filter(item => item.rank === 2),
        3: this.verseListSP.filter(item => item.rank === 3),
        4: this.verseListSP.filter(item => item.rank === 4),
        5: this.verseListSP.filter(item => item.rank === "sp"),
      };
    },
    // 
    himitsuVersionDropBoxLabel() {
        return this.selectedHimitsuVersion === 0 ? '' : '';
    },
    // 
    himitsuGetDropBoxLabel() {
        return this.selectedHimitsuGet === 0 ? '' : '';
    },
    // 
    himitsuRankDropBoxLabel() {
        return this.selectedHimitsuRank === 0 ? '' : '';
    },
    // 
    himitsuCharDropBoxLabel() {
        return this.selectedHimitsuChar === 0 ? '' : '';
    },
    // 
    verseVersionDropBoxLabel() {
        return this.selectedVerseVersion === 0 ? '' : '';
    },
    // 
      verseGetDropBoxLabel() {
        return this.selectedVerseGet === 0 ? '' : '';
    },
    // 
      verseRankDropBoxLabel() {
        return this.selectedVerseRank === 0 ? '' : '';
    },
    // 
    verseBrandDropBoxLabel() {
        return this.selectedVerseBrand === 0 ? '' : '';
    },
  },
  methods: {
    dynamicClass(data) {
      return {
        isClicked: this.selectedItems.includes(data.value),
        cordeButtonMobile: this.mobile,
        cordeButton: !this.mobile
      };
    },
    change(num) {
      this.isActive = num;
      this.selectedHimitsuVersion = iniVersion;
      this.selectedHimitsuGet = 0;
      this.selectedHimitsuRank = 0;
      this.selectedHimitsuChar = 0;
      this.himitsuItemName = "";
      this.selectedVerseVersion = iniVersion;
      this.selectedVerseGet = 0;
      this.selectedVerseRank = 0;
      this.selectedVerseBrand = 0;
      this.verseItemName = "";
    },
     toggleHimitsuItem(value) {
        const wishItemHimitsuIndex = this.wishHimitsuItems.indexOf(value);
        const itemIndex = this.selectedItems.indexOf(value);
        if (this.wishHimitsuMode) {
          if (wishItemHimitsuIndex === -1) {
            // 
            this.wishHimitsuItems.push(value);
            localStorage.setItem('wishHimitsuItems', JSON.stringify(this.wishHimitsuItems));
          } else {
            // 
            this.wishHimitsuItems.splice(wishItemHimitsuIndex, 1);
            // 
            localStorage.setItem('wishHimitsuItems', JSON.stringify(this.wishHimitsuItems));
          }
        } else {
          if (itemIndex === -1) {
            // 
            this.selectedItems.push(value);
            localStorage.setItem('selectedItems', JSON.stringify(this.selectedItems));
          } else {
            // 
            this.selectedItems.splice(itemIndex, 1);
            // 
            localStorage.setItem('selectedItems', JSON.stringify(this.selectedItems));
          }
        }
      },
      toggleVerseItem(value) {
        console.log(this.wishVerseItems)
        const wishItemVerseIndex = this.wishVerseItems.indexOf(value);
        const itemIndex = this.selectedItems.indexOf(value);
        if (this.wishVerseMode) {
          if (wishItemVerseIndex === -1) {
            // 
            this.wishVerseItems.push(value);
            localStorage.setItem('wishVerseItems', JSON.stringify(this.wishVerseItems));
          } else {
            // 
            this.wishVerseItems.splice(wishItemVerseIndex, 1);
            // 
            localStorage.setItem('wishVerseItems', JSON.stringify(this.wishVerseItems));
          }
        } else {
          if (itemIndex === -1) {
            // 
            //this.wishHimitsuItems.splice(wishItemHimitsuIndex, 1);
            // 
            this.selectedItems.push(value);
            // 
            localStorage.setItem('selectedItems', JSON.stringify(this.selectedItems));
          } else {
            // 
            this.selectedItems.splice(itemIndex, 1);
            // 
            localStorage.setItem('selectedItems', JSON.stringify(this.selectedItems));
          }
        }
      },
      loadSelectedItems() {
        const savedItems = localStorage.getItem('selectedItems');
        const savedWishHimitsuItems = localStorage.getItem('wishHimitsuItems');
        const savedWishVerseItems = localStorage.getItem('wishVerseItems');
        if (savedItems) {
          // 
          this.selectedItems = JSON.parse(savedItems);
        }
        if (savedWishHimitsuItems) {
          this.wishHimitsuItems = JSON.parse(savedWishHimitsuItems);
        }
        if (savedWishVerseItems) {
          this.wishVerseItems = JSON.parse(savedWishVerseItems);
        }
      },
      isMobile() {
        var userAgent = window.navigator.userAgent.toLowerCase()
        if (
           userAgent.indexOf("iphone") != -1 ||
          userAgent.indexOf("ipad") != -1 ||
          userAgent.indexOf("android") != -1 ||
          userAgent.indexOf("mobile") != -1
        ) {
          return true
        } else {
          return false
        }
       },
      pageTop() {
          window.scrollTo({
              top: 0,
              behavior: 'smooth'
          })
      },
      scrollWindow() {
          const top = 100 //top100px
          this.scroll = window.scrollY //
          if (top <= this.scroll) {
              this.buttonActive = true
          } else {
              this.buttonActive = false
          }
      },
      async login() {
        const provider = new GoogleAuthProvider();
        //const auth = getAuth();
        try {
          // 
          await setPersistence(auth, browserLocalPersistence);
          const result = await signInWithPopup(auth, provider);
          this.user = result.user;

          // 
          this.userPhotoURL = this.user.photoURL; // GoogleURL
 
          // 
          await this.fetchUserData(this.user.uid);
 
          // 
          const storedItems = localStorage.getItem('selectedItems');
          if (storedItems) {
            this.selectedItems = JSON.parse(storedItems);
            //console.log(":", this.selectedItems);
          }
        } catch (error) {
          console.error(":", error);
        }
        this.showUser = false;
      },
      checkUserState() {
        //const auth = getAuth();
        // 
        onAuthStateChanged(auth, (user) => {
          this.isLoading = false; // 
          if (user) {
            this.isLoggedIn = true;
            this.userPhotoURL = user.photoURL; // GoogleURL
            this.user = user; // 
            //console.log(":", user.uid);
          } else {
            this.isLoggedIn = false;
            this.userPhotoURL = null; // 
            this.user = {}; // 
            //console.log("");
          }
        });
      },
 
     async fetchUserData(uid) {
       const db = getFirestore();
       const docRef = doc(db, "users", uid);
       const docSnap = await getDoc(docRef);
 
       if (docSnap.exists()) {
         // 
         //console.log(":", docSnap.data());
       } else {
         // 
         //console.log("");
         const defaultData = { selectedItems: [] }; // 
         await this.saveUserData(uid, defaultData);
         this.selectedItems = defaultData.selectedItems; // 
         localStorage.setItem("selectedItems", JSON.stringify(this.selectedItems));
         this.showHelpPopup = true;
       }
      },
      togglePopup() {
        this.showUser = !this.showUser; // /
      },
      toggleHelpPopup() {
        console.log(this.selectedVerseRank);
        this.showHelpPopup = !this.showHelpPopup; // /
        // gat
        event('');
      },
      handleClickOutside(event) {
        // 
        const popup = this.$refs.popup;
        const icon = this.$refs.icon;
        if (this.showPopup && popup && !popup.contains(event.target) && icon && !icon.contains(event.target)) {
          console.log("")
          this.closePopup();
        }
      },
      async logout() {
        // gat
        event('');
        //const auth = getAuth();
        // currentUser
        if (auth.currentUser) {       
          // 
          await signOut(auth);
          //localStorage.removeItem("selectedItems"); // 
          // 
          //this.selectedItems = []; // 
          this.closePopup(); // 
        } else {
          //console.log("");
        }
      },
 
      // ID
      async saveUserData(uid, data) {
        const db = getFirestore();
        const docRef = doc(db, "users", uid);
        await setDoc(docRef, data, { merge: true });
      },
      // 
      async saveSelectedItems(uid, selectedItems, wishHimitsuItems, wishVerseItems) {
        const db = getFirestore();
        const docRef = doc(db, "users", uid);
        await setDoc(docRef, { selectedItems: selectedItems }, { merge: true });
        await setDoc(docRef, { wishHimitsuItems: wishHimitsuItems }, { merge: true });
        await setDoc(docRef, { wishVerseItems: wishVerseItems }, { merge: true });
      },
      async clickCloud (name) {
        this.showPopup = true; // 
        this.kind = name; //
      },
      async ok () {
        if (this.kind === 'save'){
          this.saveCloudData();
        } else if (this.kind === 'get') {
          this.getCloudData();
        } else {
          this.delete();
        }
      },
      async no () {
        this.showPopup = false; // 
      },
      async getCloudData () {
        // Firestore
        // 
        const db = getFirestore();
        const docRef = doc(db, "users", this.user.uid);
        const docSnap = await getDoc(docRef);
        if (docSnap.data().selectedItems) {
           this.selectedItems = docSnap.data().selectedItems;
           localStorage.setItem("selectedItems", JSON.stringify(this.selectedItems));
        }
        if (docSnap.data().wishHimitsuItems) {
           this.wishHimitsuItems = docSnap.data().wishHimitsuItems;
           localStorage.setItem("wishHimitsuItems", JSON.stringify(this.wishHimitsuItems));
        }
        if (docSnap.data().wishVerseItems) {
           this.wishVerseItems = docSnap.data().wishVerseItems;
           localStorage.setItem("wishVerseItems", JSON.stringify(this.wishVerseItems));
        }
        this.closePopup(); // 
        this.closeUser() // 
        // gat
        event('');
      },
      async saveCloudData () {
        // Firestore
        await this.saveSelectedItems(auth.currentUser.uid, this.selectedItems, this.wishHimitsuItems, this.wishVerseItems);
        this.closePopup(); // 
        this.closeUser() // 
        // gat
        event('');
      },
      async delete () {
        localStorage.removeItem('selectedItems'); // 
        this.closePopup(); // 
        this.closeUser() // 
      },
      closeUser () {
        this.showUser = false; // 
        this.showWishHimitsu = false;
        this.showWishVerse = false;
      },
      closePopup () {
        this.showPopup = false; // 
      },
      closeHelpPopup () {
        this.showHelpPopup = false; // 
      },
      disableScroll() {
        document.body.style.overflow = 'hidden'; // 
      },
      enableScroll () {
        document.body.style.overflow = ''; // 
      },
      wishHimitsu () {
        this.showWishHimitsu = true;
      },
      backWishHimitsu () {
        this.showWishHimitsu = false;
      },
      wishVerse () {
        this.showWishVerse = true;
      },
      backWishVerse () {
        this.showWishVerse = false;
      },
      toggleHimitsuWishMode () {
        this.wishHimitsuMode = !this.wishHimitsuMode; // /
      },
      toggltoggleVerseWishModeeMode () {
        this.wishVerseMode = !this.wishVerseMode; // /
      },
    },
    watch: {
      showUser(newValue) {
        if (newValue) {
          this.disableScroll();
        } else {
          this.enableScroll();
        }
      },
      showHelpPopup(newValue) {
        if (newValue) {
          this.disableScroll();
        } else {
          this.enableScroll();
        }
      },
    },
    mounted() {
      this.$nextTick(() => {
        // 
        this.change(this.isActive);

        // Cookies.remove('selectedItems'); // 
        this.loadSelectedItems();
        if (this.isMobile()) {
          this.mobile = true;
        } else {
          this.mobile = false;
        }
        window.addEventListener('scroll', this.scrollWindow);
        // 
        this.checkUserState();
        document.addEventListener('click', this.handleClickOutside); // 
        // 
        window.scrollTo(0, 0);

        // 
        window.addEventListener('beforeunload', () => {
          window.scrollTo(0, 0);
        });
      });
    },
    beforeUnmount() {
      window.removeEventListener('scroll', this.handleScroll);
      document.removeEventListener('click', this.handleClickOutside); // 
      if (this.observer) {
        this.observer.disconnect();
      }
      // beforeunload
      window.removeEventListener('beforeunload', () => {
        window.scrollTo(0, 0);
      });
    },
    created() {
    },
  };
</script>
 
